import React from 'react';
import ReactDOM from 'react-dom';
import firebase from './firebase';

const submitButton = {
  backgroundColor: '#7767F4',
  color: 'white',
  fontSize: 20,
  padding: 5,
  borderRadius: 5,
  fontFamily: 'Montserrat'
}


const pCSS = {
  paddingTop:25
}


class sellForm extends React.Component{


  constructor() {
    super();
    this.state = {
      make: '',
      model: '',
      year: '',
      miles: '',
      price: '',
      items: []
    }
    this.handleChange = this.handleChange.bind(this);
    this.handleSubmit = this.handleSubmit.bind(this);
  }


  handleChange(e) {
  this.setState({
    [e.target.name]: e.target.value
  });
  }


  handleSubmit(e) {
    e.preventDefault();
    const itemsRef = firebase.database().ref('items');

    // Creates the object to push data to.

    const item = {
      make: this.state.make,
      model: this.state.model,
      year: this.state.year,
      miles: this.state.miles,
      price: this.state.price,
    }
    itemsRef.push(item);
    this.setState({
      make: '',
      model: '',
      year: '',
      miles: '',
      price: ''
    });
  }




    render() {
        return (
            <div style={{paddingTop: 4}}>
                <h1 style={{padding: 45}}>Sell</h1>
                <form onSubmit={this.handleSubmit}>
                    <p style={pCSS}><input type='text' placeholder='Make' name='make' onChange={this.handleChange} value={this.state.make}/></p>
                    <p style={pCSS}><input type='text' placeholder='Model' name='model' onChange={this.handleChange} value={this.state.model}/></p>
                    <p style={pCSS}><input type='text' placeholder='Year' name='year' onChange={this.handleChange} value={this.state.year}/></p>
                    <p style={pCSS}><input type='text' placeholder='Miles' name='miles' onChange={this.handleChange} value={this.state.miles}/></p>
                    <p style={pCSS}><input type='text' placeholder='Price' name='price' onChange={this.handleChange} value={this.state.price}/></p>
                    <p style={pCSS}><button style={submitButton}>Submit</button></p>
                </form>
            </div>
        );
    }
}


export default sellForm;
