import React from 'react';
import ReactDOM from 'react-dom';
import firebase from './firebase';


class buyList extends React.Component{

// Initatizes the state to an empty array of items.

  constructor() {
    super();
    this.state = {
      items: []
    }
}

// Pulls the submit data from Firebase and also sets the new state to the data that was pulled from Firebase.

    componentDidMount() {
        const itemsRef = firebase.database().ref('items');
        itemsRef.on('value', (snapshot) => {
          let items = snapshot.val();
          let newState = [];
          for (let item in items) {
            newState.push({
              id: item,
              make: items[item].make,
              model: items[item].model,
              year: items[item].year,
              miles: items[item].miles,
              price: items[item].price
            });
          }
          this.setState({
            items: newState
          });
        });
      }

// Renders the component using the updated state. When the state is updated it maps it to items.

    render() {
        console.log(this.props.listings);
        return (
          <div style={{paddingTop: 4}}>
              <h1 style={{padding: 45}}>Buy</h1>
              <section>
                <div>
                    <ul>
                      {this.state.items.map((item) => {
                        return (
                            <li key={item.id}>
                              <p>Make: {item.make}</p>
                              <p>Model: {item.model}</p>
                              <p>Year: {item.year}</p>
                              <p>Miles: {item.miles}</p>
                              <p>Price: {item.price}</p>
                            </li>
                          )
                      })}
                    </ul>
                </div>
              </section>
          </div>
        );
    }
}


export default buyList;
